:root {
  --brand-navy: #00246B;
  --brand-light-blue: #CADCFC;
  --brand-white: #ffffff;
  --brand-black: #000000;
  --brand-navy-dark: #001a4f;
  --brand-navy-light: #003380;
  --primary: #00246B;
  --primary-foreground: #ffffff;
  --secondary: #CADCFC;
  --secondary-foreground: #00246B;
  --accent: #CADCFC;
  --accent-foreground: #00246B;
  --background: #ffffff;
  --foreground: #000000;
  --muted: #CADCFC;
  --muted-foreground: #00246B;
  --border: #CADCFC;
  --input: #CADCFC;
  --ring: #00246B;
  --radius: 0.5rem;
}
@keyframes gradient-shift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}
@keyframes glow {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-space-grotesk), var(--font-sans), Arial, Helvetica, sans-serif;
}
.text-gradient {
  background: linear-gradient(135deg, #00246B 0%, #003380 50%, #CADCFC 100%);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient-shift 5s ease infinite;
}
.gradient-blur {
  position: absolute;
  border-radius: 50%;
  filter: blur(100px);
  opacity: 0.3;
  pointer-events: none;
}
.glass-effect {
  background: #00246B;
  color: white;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid #00246B;
}
.hover-lift {
  transition: transform 0.3s cubic-bezier(0.22, 1, 0.36, 1);
}
.hover-lift:hover {
  transform: translateY(-8px);
}
@media (min-width: 1024px) {
  * {
    cursor: none !important;
  }
}
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #00246B 0%, #003380 100%);
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #001a4f 0%, #00246B 100%);
}
